<template>
  <div :id="id"></div>
</template>

<script>
import mixinChart from '@/mixins/chart.js'
import { percentFormat, floatIntFormat } from '@/utils/index'

export default {
  name: 'g2-liquidfill',
  mixins: [mixinChart],
  props: {
    // 
    data: {
      type: Array,
      default: () => {
        return [{ name: '', value: 0.3 }]
      }
    },
    // 
    axisName: {
      type: Object,
      default: () => {
        return {
          name: 'name',
          value: 'value'
        }
      }
    },
    // 
    maxValue: {
      type: Number,
      default: 1
    },
    // value 
    isPercent: {
      type: Boolean,
      default: true
    },
    useTooltip: {
      type: Boolean,
      default: true
    },
    // Canvas 
    padding: {
      type: Array,
      default: function () {
        return ['auto', 'auto']
      }
    },
    // 
    guideText: {
      type: Object,
      default: function () {
        return {
          color: 'rgba(0,0,0,0.65)',
          fontSize: 30
        }
      }
    },
    // 
    backgroundColor: {
      type: String,
      default: '#1890FF'
    }
  },
  methods: {
    setChartConfig: function (data) {
      // 
      let _this = this
      let scaleConfig = (function () {
        let obj = {}
        for (const key in _this.axisName) {
          if (_this.axisName.hasOwnProperty(key)) {
            obj[key] = {}
            obj[key]['alias'] = _this.axisName[key]
            if (key === 'value') {
              obj[key]['max'] = _this.maxValue
              obj[key]['min'] = 0
            }
          }
        }
        return obj
      }())
      //  chart 
      this.chart.source(data, scaleConfig)

      // 
      this.chart.legend(false)

      // 
      this.chart.axis(false)

      // 
      this.chart.interval().position('name*value').color(this.backgroundColor)
        .shape('liquid-fill-gauge').style({
          lineWidth: 2,
          opacity: 0.65
        })

      // tooltip
      if (this.useTooltip) {
        // tooltip
        this.chart.tooltip(true)
      } else {
        this.chart.tooltip(false)
      }

      // 
      const guideContent = this.isPercent ? percentFormat(this.data[0].value) : floatIntFormat(this.data[0].value)
      this.chart.guide().text({
        // 
        top: true,
        position: {
          name: this.data[0].name,
          value: this.maxValue / 2
        },
        // 
        content: guideContent,
        // 
        style: {
          fill: this.guideText.color,
          fontSize: this.guideText.fontSize,
          textAlign: 'center'
        }
      })
    }
  }
}
</script>
