<template>
  <div :id="id"></div>
</template>

<script>
import mixinChart from '@/mixins/chart.js'

export default {
  name: 'g2-mirror-interval',
  mixins: [mixinChart],
  props: {
    // 
    data: {
      type: Array,
      default: () => [
        { name: '1997', value: 86085, type: 'America' },
        { name: '2007', value: 144776, type: 'America' },
        { name: '2017', value: 193868, type: 'America' },
        { name: '1997', value: 9616, type: 'China' },
        { name: '2007', value: 35715, type: 'China' },
        { name: '2017', value: 122503, type: 'China' }
      ]
    },
    // 
    axisName: {
      type: Object,
      default: () => {
        return {
          name: 'name',
          value: 'value',
          type: 'type'
        }
      }
    },
    //  'rgba(0, 0, 0, 0.85)'
    axisColor: {
      type: Object,
      default: () => {
        return {
          labelColor: '#999'
        }
      }
    },
    useTooltip: {
      type: Boolean,
      default: true
    },
    // Canvas 
    padding: {
      type: Array,
      default: function () {
        return ['auto', 'auto']
      }
    }
  },
  methods: {
    setChartConfig: function (data) {
      // 
      let _this = this
      let scaleConfig = (function () {
        let obj = {}
        for (const key in _this.axisName) {
          if (_this.axisName.hasOwnProperty(key)) {
            obj[key] = {}
            obj[key]['alias'] = _this.axisName[key]
            obj[key]['sync'] = true
          }
        }
        return obj
      }())
      //  chart 
      this.chart.source(data, scaleConfig)

      const labelColor = this.axisColor.labelColor
      //  mirror 
      this.chart.facet('mirror', {
        fields: ['type'],
        //  transpose  true
        transpose: true,
        // 
        colTitle: {
          offsetY: -15,
          style: {
            fontSize: 14,
            textAlign: 'center',
            fill: labelColor
          }
        },
        eachView (view) {
          // name 
          view.axis('name', {
            position: 'top',
            line: null,
            tickLine: null,
            title: null,
            label: {
              autoRotate: false,
              textStyle: {
                fill: labelColor
              }
            }
          })
          //  value 
          view.axis('value', false)

          // 
          view.interval().size(25).position('name*value').color('type')
        }
      })
      // 
      this.chart.legend(false)

      // tooltip
      if (this.useTooltip) {
        // tooltip
        this.chart.tooltip(true)
      } else {
        this.chart.tooltip(false)
      }
    }
  }
}
</script>
